.work__container {
  width: 500px;
  min-height: 65px;
  background-color: #7b26eb;
  margin: 5px 0;
  padding: 8px 15px 8px 20px;
  display: flex;
  justify-content: space-between;
  align-items: center;
  border-radius: 7px;
  border: solid white;;
  cursor: pointer;
}

/*text*/

.work__text {
  width: 100%;
  height: 100%;
  font-size: 1.2rem;
  display: flex;
  align-items: center;
  overflow-wrap: anywhere;
}


.work__container:nth-child(3n + 1) {
  background-color:  #260b49;
}

.work__container:nth-child(3n + 2) {
  background-color:  #3d1275;
}

.work__container:nth-child(3n + 3) {
  background-color:  #5419a1;
}

/*icon*/

.work__icon {
  width: 25px;
  height: 25px;
  margin: 5px;
  cursor: cell;
}

/*work completed*/
.work__container.completed {
  background-color: #5d00d6;
  text-decoration: line-through;
}

@media (width<426px) {
	.work__container {
		width: 250px;
	}
}
